<template>
  <skeleton v-if="!isSkeletonLoading" :nums="4" fheight="4rem" iheight="2rem"></skeleton>
  <div v-else class="content">
    <div class="order-info">
      <div class="order-left">
        <img :src="list.productImg | imageToHttps" alt=""/>
      </div>
      <div class="order-right">
        <div class="title">{{list.comment.productName}}</div>
        <div class="attr" v-if="list.productSpecification">{{list.productSpecification}}</div>
      </div>
    </div>
    <div class="list">
      <div class="part-one">
        <div class="left">已{{star}}</div>
        <div class="right">{{createTime | dateFormat}}</div>
      </div>
      <div class="part-two">{{list.comment.commentContent}}</div>
      <div class="part-three" v-if="list.comment.commentPics.length !== 0">
        <img :src="item.picUrl | imageToHttps" alt="" v-for="item in list.comment.commentPics" :key="item.commentSharePicId" @click="lookStart(item)"/>
      </div>
    </div>
    <div class="list-track" v-if="showExperienceContent">
      <div class="part-one">
        <div class="left">已追评</div>
        <div class="right">{{experienceTime | dateFormat}}</div>
      </div>
      <div class="part-two">{{list.comment.experienceContent}}</div>
      <div class="part-three" v-if="list.comment.experienceContent.length !== 0 && list.comment.experiencePics.length>0">
        <img :src="i.picUrl | imageToHttps" alt="" v-for="i in list.comment.experiencePics" :key="i.commentSharePicId" @click="look(i)"/>
      </div>
    </div>
    <span class="bottom-tip">已经到底啦~</span>
    <div class="bottom" @click="toChase" v-if="showExperienceBtn">
      <div>追加评价</div>
    </div>
  </div>
</template>
<script>
import personalApi from '@/http/service/index';
import skeleton from '@/components/skeleton/index.vue'

import Vue from 'vue';
import { ImagePreview } from 'vant';
Vue.use(ImagePreview);
export default {
  components: {
    skeleton
  },
  created() {
    const data = {
      cOrderSn: this.$route.query.cOrderSn
      // cOrderSn: 'WD192414810121'
    }
    personalApi.getComment(data).then(res => {
      this.commentImgs = []
      this.experienceImgs = []
      if (res.data.success) {
        this.list = res.data.data
        this.list.comment.experiencePics.forEach(m => {
          this.experienceImgs.push(m.picUrl)
        })
        this.list.comment.commentPics.forEach(n => {
          this.commentImgs.push(n.picUrl)
        })
        this.createTime = res.data.data.comment.createTime
        this.experienceTime = res.data.data.comment.experienceTime
        switch (res.data.data.comment.star) {
          case 1:
            this.star = '差评'
            break;
          case 3:
            this.star = '中评'
            break;
          case 5:
            this.star = '好评'
            break;
        }

        if (res.data.data.isCanExperience) {
          this.showExperienceBtn = true
        } else {
          this.showExperienceBtn = false
        }
        if (res.data.data.isExperienced) {
          this.showExperienceContent = true
        } else {
          this.showExperienceContent = false
        }
        this.isSkeletonLoading = true
      } else {
        this.$toast.fail(res.data.message)
      }
    })
  },
  data() {
    return {
      commentImgs: [],
      experienceImgs: [],
      ImagePreview: '',
      createTime: '',
      experienceTime: '',
      isSkeletonLoading: false, // 是否加载完成动态接口
      list: {
        comment: {
          commentContent: '',
          commentPics: [],
          experienceContent: '',
          experiencePics: [],
        }
      },
      star: '', // 好中差评
      show: true,
      showExperienceContent: false, // 展示追评内容标志
      showExperienceBtn: false, // 展示追评按钮标志
    }
  },
  filters: {
    dateFormat(value) {
      if (!value) {
        return ''
      }
      const date = new Date(value);
      const Y = date.getFullYear() + '.';
      const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '.';
      const D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + '';
      return Y + M + D;
    }
  },
  methods: {
    /**
     *追加评价
     */
    toChase() {
      this.$router.push({ path: '/review', query: { cOrderSn: this.$route.query.cOrderSn } })
    },
    /**
     *查看追评图片
     */
    look(i) {
      let index = this.experienceImgs.indexOf(i.picUrl)
      this.ImagePreview = ImagePreview(
        {
          images: this.experienceImgs,
          startPosition: index,
        }
      );
    },
    /**
     *查看初次图片
     */
    lookStart(i) {
      let index = this.commentImgs.indexOf(i.picUrl)
      this.ImagePreview = ImagePreview(
        {
          images: this.commentImgs,
          startPosition: index,
        }
      );
    }
  }
}
</script>
